




















import { Component, Prop, Vue } from "vue-property-decorator";
import { Message } from 'element-ui';

@Component({
  name: "ImgUpload"
})
export default class extends Vue {
  @Prop({default: 1}) private limit!: number;
  private dialogVisible = false;
  private dialogImageUrl = "";
  private fileName = ''
  private testFile = ''
  private base64file = ''

  private async handleRemove(file: any) {
    console.log(file);
  }
  private async handlePictureCardPreview(file: any) {
    this.dialogImageUrl = file.url;
    this.dialogVisible = true;
  }
  private async handleRequest(option: any) {
    //get file
    this.testFile = option.file
    const file = option.file
    this.fileName = file.name
    //check file status
    if(!(file.type == 'image/jpeg' || file.type != 'image/png' || file.type != 'image/JPG')){
      return Message.error('上傳的圖片格式只能為 JPG/PNG/JPEG !')
    }
    if((file.size / 1024 / 1024 )> 10){
      return Message.error('上船的圖片不能超過 10mb!')
    }
    //make base64 file str
    const reader:FileReader = new FileReader()
    reader.onload = async (e:any)=>{
      this.base64file = e.target.result
      await option.onProgress({percent: 100})
      setTimeout(()=>{
        option.onSuccess()
      },500)
    }
    if(file){
      await reader.readAsDataURL(file)
    }
  }
  private handleExceed(){
    return Message.error('圖片上傳上限為一張!')
  }
}
