















































import { Component, Prop, Vue, PropSync } from "vue-property-decorator";
import ImgUpload from '@/components/ImgUpload/index.vue'
import {UPLOAD_IMGUR} from '@/api/imgur'
import { UPDATE_PRODUCT } from '@/api/product'
import { Message, Loading } from 'element-ui';

@Component({
  name: "Dialog",
  components: {ImgUpload}
})
export default class extends Vue {
  @PropSync('open',{type: Boolean , default: false}) openDialog!: boolean;
  @Prop(String) private productTitle!: string;
  @Prop(Object) private productData!: any;
  // private openDialog = false;
  private LableWith = "120px";
  private photoLimit = 1;
  private defaultPhoto = ''
  private loadingCard = null

  private handleClose() {
    this.openDialog = false
  }
  private async handleSend() {
    //select photo
    if(!this.productData.MainPicture){
      //check photo null status
      const {fileName , base64file} = (this as any).$refs.imgAddTable
      //return error
      if(!fileName) return Message.error('圖片不能為空!!');
      //upload photo
      (this as any).loadingCard = Loading.service({fullscreen:true,text:'圖片上傳中～'})
      //photo update res
      const status:any = await this.UploadIMG(fileName,base64file)
      //photo update success
      if(status.success){
        (this as any).loadingCard = Loading.service({fullscreen:true,text:'產品更新中～～'})
        const data = this.productData
        data.MainPicture = status.data.link;
        data.ColorCode = data.ColorCode.toString();
        this.UpdateEdit({id:data.ProductId},data)
      }
      else{
        //watch error code
        (this as any).loadingCard.close()
        return Message.error('HAVE SOME ERROR NEED FIX')
      }
    }
    //no select photo 
    (this as any).loadingCard = Loading.service({fullscreen:true,text:'產品更新中～～'})
    const data:any = this.productData
    data.ColorCode = data.ColorCode.toString()
    this.UpdateEdit({id:data.ProductId},data)
  }
  private EditColor(index: number) {
    let color = this.productData.ColorCode[index];
    if (color === null) {
      this.productData.ColorCode.splice(index, 1);
    }
  }
  private addColorCode(){
    this.productData.ColorCode.push('#ffffff')
  }
  private IMGdelete(){
    this.productData.MainPicture = null
  }
  private async UpdateEdit(id:object,data:any){
    let edit_status:any = await UPDATE_PRODUCT(id,data)
    if(edit_status.code == 20000){
      this.openDialog = false;
      this.$emit('fileUpdate',data,'商品編輯成功!');
      (this as any).loadingCard.close();
    }
  }
  private async UploadIMG(fileName:string,file:any){

    const formatFile = file.split(',')[1]
    //imgurl 
    const form = new FormData();
    form.append('image',formatFile);
    form.append('type','base64');
    form.append('name',fileName);
    
    let status = await UPLOAD_IMGUR(form)
    return status
  }
  created(){
    this.loadingCard  = null
  }
}
