


































































































import { Component, Prop, Vue } from "vue-property-decorator";
import DialogComponet from "@/components/Dialog/index.vue";
import { PRODUCT_STATUS, UPDATE_PRODUCT } from "@/api/product";
import { Message } from "element-ui";

@Component({
  name: "TabPane",
  components: { DialogComponet }
})
export default class extends Vue {
  @Prop({ default: "WOMEN" }) private type!: string;
  private dialogOpen = false;
  private list = null;
  private loading = false;
  private dialogStatus = false;
  private dialogTitle = "";
  private DialogForm = {};

  created() {
    this.getList();
  }
  private async updateStatus(row: any) {
    PRODUCT_STATUS({ id: row.ProductId }).then(res => {
      for (let item of (this as any).list) {
        if (item.ProductId === row.ProductId) {
          item.Status = !item.Status;
          return Message.success("商品狀態修改成功！");
        }
      }
    });
  }
  private editFile(row: any, msg: string) {
    for(let item of (this as any).list){
      if(item.ProductId === row.ProductId){
        item = row
        item.ColorCode = item.ColorCode.split(',')
        return Message.success(msg);
      }
    }
  }
  private async DelProduct(row: any) {
    // console.log(row)
    // const data = row
    // data.ColorCode = row.ColorCode.toString()
    // data.MainPicture = row.MainPicture.split('/')[5]
    // UPDATE_PRODUCT({id:row.ProductId},data)
    // .then(res=>{Message.success('測試成功')})
  }
  private async NETGET() {
    (this as any).$http
      .get(`/netapi/ProductsApi/?type=${this.type}&first=1&last=10`)
      .then(async (res: any) => {
        const { Length, Productslist } = res;
        await this.Fixdata(Productslist);
      })
      .then((success: any) => {
        this.loading = false;
      });
  }
  private Fixdata(data: any) {
    let newData = data.map((item: any) => {
      item.ColorCode = item.ColorCode.split(",");
      return item;
    });

    this.list = newData;
  }
  private async getList() {
    this.loading = true;

    await this.NETGET();
  }
  private async handleEdit(index: number, row: any) {
    this.dialogOpen = true;
    this.DialogForm = JSON.parse(JSON.stringify(row)); //deep copy
    this.dialogTitle = row.BigTitle;
  }
}
