import request from "@/utils/http";
import {IProductInfoData} from './type'
export const defaultProduct : IProductInfoData = {
  BigTitle: '',
  ColorCode: '',
  MainPicture: '',
  Name: '',
  Picture: '',
  ProductId: 0,
  SizeCode: '',
  TypeDetial: 0,
  TypeTitle: '',
  UnitPrice: 0,
  UnitsInStock: 0
}
export const GET_PRODUCT_TYPE = (params: any )=>
  request({
    url: "/netapi/ProductsApi/",
    method: "get",
    params
  });
export const DELETE_PRODUCT = (params: any )=>
request({
  url: "/netapi/ProductsApi/",
  method: "delete",
  params
});
export const UPDATE_PRODUCT = (params: any ,data:any)=>
request({
  url: "/netapi/ProductsApi/",
  method: "put",
  params,
  data
});
export const ADD_PRODUCT = (params: any )=>
request({
  url: "/netapi/ProductsApi/",
  method: "delete",
  params
});
export const PRODUCT_STATUS  = (params: any)=>
request({
  url: "/netapi/productstatus/" ,
  method: 'get',
  params
})

export const GET_ALL_PRODUCT = () =>
request({
  url: "/netapi/ProductsApi/",
  method: "get",
});

